@import url(https://db.onlinewebfonts.com/c/b77483504f720bf0ce1d3f83f694ea52?family=Pexico-Regular);

html {
  scroll-behavior: smooth;
  scrollbar-width: thin;
  scrollbar-color: #00ff7f #111; /* Cor do thumb e do track para Firefox */
}

/* Em seu arquivo App.css */
body {
  margin: 0;
  padding: 0;
  font-family: "Pexico-Regular";
  background-color: black;
}

/* --- Estilos da Barra de Rolagem (Tema Matrix) --- */

/* Para navegadores baseados em WebKit (Chrome, Safari, Edge) */
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: #111; /* Um cinza bem escuro para o trilho */
}

::-webkit-scrollbar-thumb {
  background-color: #00ff7f; /* Cor verde para a barra */
  border-radius: 10px;
  border: 2px solid #000; /* Borda preta para contraste */
  box-shadow: 0 0 5px #00ff7f; /* Efeito de brilho */
}

::-webkit-scrollbar-thumb:hover {
  background-color: #00cc66; /* Verde mais escuro no hover */
  box-shadow: 0 0 10px #00ff7f;
}

/* Deixe as regras para 'body' e '.App' como estão */

.background-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1; /* Coloca o background atrás de todo o conteúdo */
  overflow: hidden; /* Evita barras de rolagem do próprio background */

}

header {
  position: sticky;
  background-color: black;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
  box-sizing: border-box; /* Garante que o padding não aumente a largura total */
}

.logo {
  font-size: 2em;
  color: #00FF7F; /* Cor verde do tema */
  font-weight: 600;
  text-shadow: 0 0 5px #00FF7F;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav li {
  margin-left: 20px;
}

nav a {
  text-decoration: none;
  color: #fff;
  font-weight: 500;
  font-size: 1.1em;
  transition: color 0.3s, text-shadow 0.3s;
}

nav a:hover {
  color: #00FF7F;
  text-shadow: 0 0 5px #00FF7F;
}

/* Em seu arquivo App.css */

/* Deixe as regras para 'body', '.App', e '.background-container' como estão */

/* Em seu arquivo App.css */

/* Deixe as regras para 'body', '.App', e '.background-container' como estão */

.main-container {
  position: absolute;
  top: 0;
  left: 0;  /* Necessário para o cálculo da margem automática */
  right: 0; /* Adicionado para permitir a centralização horizontal */
  width: 80%;
  height: 80%;
  z-index: 2;
  background-color: black;
  margin: 100px auto 0; /* Centraliza horizontalmente e mantém a margem superior */
  border-radius: 40px;

  padding: 15px;


  /* Layout flex para alinhar os containers */
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4rem;
}

/* O container do texto precisa de um tamanho definido */
.text-ascii-container {
  position: relative; /* Cria um contexto para o texto absoluto dentro dele */
  width: 500px; /* Dê a ele uma largura fixa */
  height: 250px; /* E uma altura fixa */
  
}

.left-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center; /* Centraliza o texto dentro do DecryptedText também */
}

.decrypted-text-wrapper {
  text-align: justify;
  font-size: 30px;
  margin-top: 4rem;
  margin-left: 4rem;
  max-width: 500px; /* Ajuda na quebra de linha */
  color: white !important;
}

/* O container do card não precisa de regras extras, pois o componente
   ProfileCard já tem seu próprio tamanho definido internamente. */


.sobreMim-container{
  display: flex;
  flex-direction: column;
  margin: 10%;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 80%;
  min-height: 720px; /* Usar min-height para mais flexibilidade */
  scroll-margin-top: 100px; /* Adicionado para compensar o header */

  
}

.buttons-sobreMim {
  display: flex;
  justify-items: center;
  gap: 1rem;
  margin-top: 2rem;
}

.btn-social-item {
  display: inline-flex;
  align-items: center;
  padding: 0.7rem 1.2rem;
  border: 2px solid #00FF7F;
  color: #00FF7F;
  text-decoration: none;
  font-size: 1.2em;
  font-family: 'Pexico-Regular', sans-serif;
  transition: background-color 0.3s, color 0.3s;
  border-radius: 5px;
   text-shadow: 0 0 5px #00FF7F;
}

.btn-social-item:hover {
  background-color: #00FF7F;
  color: black;
}

.btn-social-item i {
  margin-right: 0.5rem;
  font-size: 1.5rem;
}

.placeholder-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 500px; /* Altura de exemplo */
  width: 100%;
  color: white;
  scroll-margin-top: 100px; /* Compensa o header */
}

.projetos-container {
  position: relative; /* Adicionado para ser o contexto de posicionamento do background */
  display: flex;
  flex-direction: column; /* Empilha os itens verticalmente */
  align-items: center;    /* Centraliza os itens horizontalmente */
  justify-content: center;
  gap: 4rem;              /* Adiciona um espaço entre o texto e a pasta */
  padding: 5rem 0;        /* Adiciona um espaçamento vertical */
  min-height: 100vh;
  text-align: center;
  scroll-margin-top: 100px;
  height: auto;
  width: 100%; /* Garante que o container ocupe toda a largura */
  overflow-x: hidden; /* Impede que componentes filhos (ex: ScrollVelocity) causem rolagem na página */
}

.custom-scroll-text {
  color: #00FF7F;
  text-shadow: 0 0 8px #00FF7F;
  font-size: 5rem; /* Tamanho de fonte grande para desktop */
  
}

.projects-caixa {
  width: 90%; /* Define a largura da "caixa" */
  max-width: 1200px; /* Um limite para telas muito grandes */
  margin: 0 auto; /* Centraliza a caixa */
  overflow-x: scroll; /* Força a barra de rolagem a ser sempre visível, deixando claro que a área é rolável */
  overflow-y: hidden;
  padding-bottom: 1rem; /* Espaço para a barra de rolagem não colar nos cards */
}

/* Estilos para a barra de rolagem da caixa de projetos */
.projects-caixa::-webkit-scrollbar {
  height: 8px;
}

.projects-caixa::-webkit-scrollbar-thumb {
  background-color: rgba(0, 255, 127, 0.7);
  border-radius: 10px;
}

.projects-caixa::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.3);
}

.projects-grid {
  display: inline-flex; /* Usa inline-flex para que a largura se ajuste ao conteúdo */
  align-items: center; /* Centraliza os cards verticalmente na linha */
  gap: 2rem;
  padding: 2rem; /* Aumenta o padding para melhor espaçamento */
  box-sizing: border-box;
  min-height: 300px; /* Garante uma altura mínima para a área dos cards */
}

.background-dither{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; /* Coloca o glitch atrás do conteúdo da seção */
  overflow: hidden; /* Garante que o glitch não cause overflow */
}
/* --- Media Queries para Responsividade --- */

@media (max-width: 1200px) {
  .main-container {
    gap: 2rem; /* Diminui o espaço entre os itens em telas médias */
  }
}

@media (max-width: 992px) {
  /* Ajusta o cabeçalho para telas menores */
  header {
    padding: 20px 5%;
  }

  /* Empilha o conteúdo principal verticalmente */
  .main-container {
    flex-direction: column;
    height: auto; /* Permite que a altura cresça com o conteúdo */
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
    top: 80px;
    margin-top: 0;
    padding: 2rem;
  }

  .text-ascii-container {
    width: 100%;
    max-width: 450px;
    height: 200px;
  }

  .decrypted-text-wrapper {
    margin-left: 0;
    font-size: 24px;
    text-align: center;
    margin-top: 2rem;
  }

  .sobreMim-container {
    height: auto; /* Deixa o conteúdo definir a altura */
    min-height: 50vh;
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
}

/* Dentro do seu App.css */

@media (max-width: 768px) {
  .custom-scroll-text {
    font-size: 3.5rem;
  }

  header {
    flex-direction: column; /* Empilha o logo e a navegação */
    gap: 1rem;
    padding: 20px 2%;
  }

  nav ul {
    justify-content: center;
    flex-wrap: wrap;
    gap: 0 15px;
  }

  nav li {
    margin-left: 0;
  }

  /* ... (suas outras regras para header, etc. podem continuar aqui) ... */
.background-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 730px;
  z-index: -1; /* Coloca o background atrás de todo o conteúdo */
  overflow: hidden; /* Evita barras de rolagem do próprio background */

}

  .main-container {
    width: 90%; /* Usa largura percentual para ser responsivo */
    margin: 40px auto 0; /* Simplifica e mantém a centralização */
    padding: 2rem;
    box-sizing: border-box;
  }

  .sobreMim-container {
    width: 80%;
    margin-left: auto; /* Mantém a centralização */
    margin-right: auto; /* Mantém a centralização */

    padding: 5rem 1rem;
  }

.text-ascii-container {
  
  width: 100%; /* Adapta-se ao contêiner pai */
  height: 150px; /* E uma altura fixa */
  
}

}

@media (max-width: 480px) {
  .decrypted-text-wrapper {
    font-size: 18px;
  }

  .custom-scroll-text {
    font-size: 2.5rem;
  }

  .logo {
    font-size: 1.5em;
  }

  .buttons-sobreMim {
    flex-direction: column;
    align-items: center;
  }

  .btn-social-item {
    width: 80%;
    justify-content: center;
  }
}

/* Oculta o cursor customizado em telas menores (mobile) */
@media (max-width: 768px) {
  .cursor-wrapper {
    display: none;
  }

 
}

