/* EFEITO DE PARTÍCULAS ANIMADAS         */
/* ============================================= */

/* Animação que faz as partículas se moverem de cima para baixo */
@keyframes anim-particles {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-2000px);
  }
}
