.loadingScreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; /* Usa 100% em vez de 100vw para evitar overflow causado pela barra de rolagem */
  height: 100vh;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  opacity: 1;
  transition: opacity 1s ease-out, visibility 1s ease-out;
  visibility: visible;
  font-family: 'Pexico-Regular', monospace;
}

.loadingScreen.fadeOut {
  opacity: 0;
  visibility: hidden;
}

.textContainer {
  width: 80%;
  max-width: 700px;
  overflow-x: hidden; /* Impede que o texto longo quebre o layout no mobile */
}

.loadingText {
  color: #00FF7F;
  text-shadow: 0 0 5px #00FF7F;
  font-size: 1.5rem;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid #00FF7F;
  margin: 1rem 0;
  animation: typing 2s steps(40, end);
  visibility: hidden; /* Começa invisível */
}

/* Animações em sequência */
.loadingText { animation-name: typing; animation-timing-function: steps(40, end); animation-fill-mode: forwards; }
.loadingText.delay1 { animation-duration: 2s; animation-delay: 2s; }
.loadingText.delay2 { animation-duration: 3s; animation-delay: 4s; }
.loadingText.delay3 { animation-duration: 1.5s; animation-delay: 7s; }

@keyframes typing {
  from {
    width: 0;
    visibility: visible;
  }
  to {
    width: 100%;
    visibility: visible;
  }
}

.blink {
  animation:
    typing 1.5s steps(30, end) 7s forwards,
    blink-caret .75s step-end infinite 7s;
}

@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: #00FF7F; }
}

@media (max-width: 768px) {
  .loadingText {
    font-size: 1.1rem;
    /* A remoção das regras anteriores faz com que o elemento herde
       a animação de digitação original da classe base, restaurando o efeito
       desejado no mobile. */
  }
}